#cookies-alert {
  bottom: -300px;
  background-color: #000;
  color: #fff;
  display: none;
  font-size: 0.8em;
  padding: 20px;
  position: fixed;
  right: 20px;
  transition: bottom 400ms ease-in-out;
  width: 280px;
  z-index: 9000;

  &.block {
    display: block; }

  &.show {
    bottom: 20px; } }
